import React, { useState, useEffect } from "react";
import Modal from "react-awesome-modal";
import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "../../redux/data/dataActions";
import Spinner from "../Spinner";

function FlagsControl(props) {
  const { accionName, colorBoton, funcionBC } = props;

  const [respuesta, setRespuesta] = useState("");
  const [actualFlag, setActualFlag] = useState("");
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const dispatch = useDispatch();
  const data = useSelector((state) => state.data);
  const blockchain = useSelector((state) => state.blockchain);

  useEffect(() => {}, []);

  const getData = async (key) => {
    setLoading(true);
 
    let ret = await funcionBC.call({ from: blockchain.account });

    console.log("**** >>>", ret);

    setLoading(false);
    if (ret === true || ret === false) {
      setRespuesta(ret ? "SI" : "NO");
    } else {
      let respuesta = JSON.stringify(ret);
      if (respuesta.length > 0) {
        if (respuesta[0] != "{" && respuesta[0] != "[") {
          respuesta = ret;
        }
      }
      setRespuesta(respuesta);
    }
    setVisible(true);
  };

  return (
    <div>
      {!loading ? (
        <button
          style={{
            padding: 7,
            borderRadius: 5,
            marginLeft: 10,
            backgroundColor: colorBoton,
            marginBottom: 5,
          }}
          onClick={() => getData()}
        >
          {accionName}
        </button>
      ) : (
        <>
          <Spinner />
        </>
      )}

      <Modal
        visible={visible}
        width="990"
        height="600"
        effect="fadeInUp"
        onClickAway={() => setVisible(false)}
      >
        <div>
          <div style={{ color : 'black', textAlign: 'center', marginTop : 30 }}>
            <h3> {accionName}</h3>
            <div style={{ width : 980, height : 520,  overflowY : 'scroll' }}>
            <span> {respuesta} </span>
            </div>
          </div>
         
        </div>
      </Modal>
    </div>
  );
}

export default FlagsControl;
