import React, { useState, useEffect } from "react";

import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";
import Popup from "./Popup";
import ReactTooltip from "react-tooltip";
import Spinner from "./Spinner";
import moment from "moment";

import "./Header.css";
// import styled from 'styled-components';

const Header = ({ actualPage }) => {
  const dispatch = useDispatch();
  const data = useSelector((state) => state.data);
  const blockchain = useSelector((state) => state.blockchain);

  const [timeLeft, setTimeLeft] = useState("");
  const [loading, setLoading] = useState(false);
  const [isShowLogin, setIsShowLogin] = useState(false);
  const [statusOK, setStatusOK] = useState(false);
  const [statusError, setStatusError] = useState(false);
  const [message, setMessage] = useState("Transaction in progress");

  // console.log("DATA completa", data);

  var x = setInterval(function () {
    if (data && data.actualTime && data.startGame) {
      const bc_startGameTime = parseInt(data.startGame);
      // const bc_actualTime = parseInt(data.actualTime);
      const bc_getDayNumerOfGame = parseInt(data.getDayNumerOfGame);

      // let startGameTime = moment.unix(bc_startGameTime).utc().format("YYYY-MM-DD hh:mm:ss A");
      //  let actualGameTime = moment.unix(bc_actualTime).utc().format("YYYY-MM-DD hh:mm:ss A");
      let actualLocalGameTime = moment.utc().format("YYYY-MM-DD hh:mm:ss A");
      //Getting the current date-time with required formate and UTC
      let finalTimeToClaim = moment
        .unix(bc_startGameTime)
        .utc()
        .add(bc_getDayNumerOfGame, "days")
        .format("YYYY-MM-DD hh:mm:ss A");

      let diffr = moment.duration(
        moment(finalTimeToClaim).diff(moment(actualLocalGameTime))
      );

      try {
        let hours = parseInt(diffr.asHours());
        let minutes = parseInt(diffr.minutes());
        let seconds = parseInt(diffr.seconds());

        setTimeLeft(hours + "h " + minutes + "m " + seconds + "s ");
      } catch (e) {
        console.log("ERROR", e);
      }
      //  console.log("SET RELOJ")

      // If the count down is finished, write some text
      // if (distance < 0) {
      // console.log("CLEARRRR *******");
      clearInterval(x);
      //}
    }
  }, 1000);

  const transactionFinished = (resultado) => {
    if (resultado) {
      setStatusOK(true);
      setStatusError(false);
      setMessage("Transaction finished");
    } else {
      setStatusOK(false);
      setStatusError(true);
      setMessage("Transaction failed");
    }

    const timer = setTimeout(() => {
      setIsShowLogin(false);
      setStatusOK(false);
      setStatusError(false);
      setMessage("Transaction in progress");
    }, 3000);
    return () => clearTimeout(timer);
  };

  useEffect(() => {
    if (isShowLogin) {
      transactionFinished(true);
    }
  }, [data]);

  const getBalance = (tot) => {
   // console.log("tot", tot);
    if (tot) {
      return parseFloat(blockchain.web3.utils.fromWei(tot, "ether")).toFixed(2);
    } else return 0;
  };

  const getMinimizeAdress = (address) => {
    if (address) {
      return (
        address.substring(0, 4) +
        "..." +
        address.substring(address.length - 7, address.length)
      );
    } else {
      return "";
    }
  };

  const getTotalClaim = () => {
    const Chicks = data.allOwnerLips;

    if (Chicks.length > 0) {
      let total = 0;

      Chicks.forEach((chick) => {
        if (chick.winnerFeed) {
          if (getIsTimeToClaim(chick)) {
            console.log("Chick: ", chick);
            total += parseFloat(getEthPrice(chick.winnerReward));
          }
        }
      });

      return parseFloat(total).toFixed(2);
    }

    return 0.0;
  };

  const getEthPrice = (tot) => {
    if (tot) {
      return parseFloat(blockchain.web3.utils.fromWei(tot, "ether")).toFixed(2);
    } else return 0;
  };

  const getIsTimeToClaim = (item) => {
    let ret = false;

    if (data.getDayNumerOfGame > item.dateFeed) {
      const dateActual = Math.floor(new Date().getTime() / 1000); // data.actualTime;
      const dateGano = parseInt(item.dateBlock);

      const diff = dateActual - dateGano;
      const diff24hs = 24 * 60 * 60;
      const diffLeft = diff24hs - diff;
      const differenceInMinutes = Math.floor(diffLeft / 60);
      const differenceInHours = Math.floor(differenceInMinutes / 60);

      if (differenceInHours < 0) {
        ret = true;
      }
    }

    return ret;
  };

  const cobrarPremioTotal = async (_account) => {
    setLoading(true);
    setIsShowLogin(true);

    blockchain.gameLogic.methods
      .cobrarTotalPremio()
      .send({
        from: _account,
      })
      .once("error", (err) => {
        setLoading(false);
        transactionFinished(false);
        console.log(err);
      })
      .then((receipt) => {
        setLoading(false);
        console.log(receipt);
        dispatch(fetchData(blockchain.account));
      });
  };

  return (
    <div>

      <div  id="claim" className="flex flex-col items-start  ">
        <header className="flex flex-row flex-nowrap justify-between w-screen">
          <nav className="w-screen bg-transparent gradiant-border-bottom z-10 backdrop-filter backdrop-blur">
            <div className="px-4 py-1.5">
              <div className="flex items-center justify-between h-16">
                <div style={{ marginTop: 28 }} className="flex items-center">
                  <Link
                    style={{ color: "blue" }}
                    to="/"
                    target="_blank"
                    rel="noopener noreferrer"
                    href="#"
                    className="text-baseline text-primary hover:text-high-emphesis focus:text-high-emphesis p-2 md:p-3 "
                    id="homepage"
                  >
                    <img
                      style={{ height: 50 }}
                      src="assets/images/logo/cool.png"
                      alt="CoolChick"
                      className=" w-auto"
                    />
                  </Link>
                  <div
                    className="hidden sm:block sm:ml-4"
                    style={{ marginLeft: 0 }}
                  >
                    <div className="flex space-x-2">
                      <Link
                        style={{ color: "yellow" }}
                        to="/"
                        aria-current="page"
                        className={
                          actualPage == "Game"
                            ? "text-yellow hover:text-high-emphesis focus:text-high-emphesis p-2 md:p-3 whitespace-nowrap text-high-emphesis"
                            : "text-baseline text-primary hover:text-yellow focus:text-yellow p-2 md:p-3 whitespace-nowrap"
                        }
                        href="/swap"
                        id="swap-nav-link"
                        style={{ fontSize: 18, fontWeight: 600 }}
                      >
                        DASHBOARD
                      </Link>
                     
                    </div>
                  </div>
                </div>
                <div className="flex flex-row items-center justify-center w-full lg:w-auto p-4 fixed left-0 bottom-0 bg-dark-1000 lg:relative lg:p-0 lg:bg-transparent">
                  <div
                    style={{ marginTop: 10, marginRight: 28 }}
                    className="flex items-center justify-between sm:justify-end space-x-2 w-full"
                  >




<div data-class="tipTimer" data-tip data-for="TipTimer"
                          style={{
                            backgroundColor: "orange",
                            cursor: "default",
                            marginRight: 7,
                            height:42
                          }}
                          className="grid grid-flow-col auto-cols-max items-center rounded-lg text-sm text-secondary py-22 px-33 pointer-events-auto"
                        >
                          <div 
                          
                          style={{
                            paddingLeft:8,
                            paddingRight:10,
                            color:"black",
                            paddingTop:4,
                            paddingBottom:4,
                            fontSize:13
                          }}
                          
                          >COUNTDOWN</div>
                          <ReactTooltip id="TipTimer" data-multiline="true" backgroundColor="#05a100" textColor="white" place="bottom"  effect="solid">
                                
                            When the account reaches zero<br></br> you can withdraw your earnings<br></br> and feed the chicks again.


                  </ReactTooltip>
                          <div
                            style={{ width:106, fontSize: 16,fontWeight:400, padding: 7,backgroundColor:"#222222",marginRight:2 }}
                            className="text-primary"
                          >
                          { actualPage == "Presale" ? (
                            "Coming soon"
                          ):(

                          timeLeft
                          )                      
                          }
                          </div>
                        </div> 





                    <div className="hidden sm:inline-block">
                      <div className="flex items-center rounded p-0.5 whitespace-nowrap text-sm font-bold cursor-pointer select-none pointer-events-auto">
                      
 

                      </div>
                    </div>
                    <div className="w-auto flex items-center rounded bg-dark-900 hover:bg-dark-800 p-0.5 whitespace-nowrap text-sm font-bold cursor-pointer select-none pointer-events-auto">
                      <div
                        className="  text-primary text-bold"
                        style={{ paddingRight: 0, fontSize: 14, padding: 10 }}
                      >
                        {!data ? (
                          <>0 BNB</>
                        ) : (
                          <>{getBalance(data.balanceToken)} BNB</>
                        )}
                      </div>
                      <img
                        style={{ paddingRight: 10 }}
                        src="assets/images/logo/cool.png"
                        alt="CoolChick"
                        className="h-6 w-auto"
                      />
                      <div
                        id="web3-status-connected"
                        className="flex items-center rounded-lg bg-dark-1000 text-sm text-secondary py-2 px-3"
                      >
                        <div className="mr-2">
                          {blockchain.account === "" ||
                          blockchain.gameLogic === null ? (
                            <span
                              style={{
                                color: "white",
                                borderStyle: "none",
                                fontSize: 14,
                              }}
                              onClick={(e) => {
                                e.preventDefault();
                                dispatch(connect());
                              }}
                            >
                              <span>Wallet connect</span>
                            </span>
                          ) : (
                            <span
                              style={{
                                color: "white",
                                borderStyle: "none",
                                fontSize: 10,
                              }}
                            >
                              {getMinimizeAdress(blockchain.account)}
                            </span>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="-mr-2 flex sm:hidden">
                  <button
                    className="inline-flex items-center justify-center p-2 rounded-md text-primary hover:text-high-emphesis focus:outline-none"
                    id="headlessui-disclosure-button-3"
                    type="button"
                    aria-expanded="false"
                  >
                    <span className="sr-only">Open main menu</span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="block h-6 w-6"
                      aria-hidden="true"
                    >
                      <title>Burger</title>
                      <line x1="3" y1="12" x2="21" y2="12"></line>
                      <line x1="3" y1="6" x2="21" y2="6"></line>
                      <line x1="3" y1="18" x2="21" y2="18"></line>
                    </svg>
                  </button>
                </div>
              </div>
            </div>
          </nav>
        </header>
      </div>
      <Popup
        isShowLogin={isShowLogin}
        message={message}
        statusError={statusError}
        statusOK={statusOK}
      />
    </div>
  );
};

export default Header;
