import React, { useEffect, useState } from "react";

import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";

import Item from "./components/presale/Item";
import ItemChick from "./components/presale/ItemChick";
import Header from "./components/Header";

function Presale() {
  // Primeros pasos en la carga de la información
  const dispatch = useDispatch();
  const data = useSelector((state) => state.data);
  const [loading, setLoading] = useState(false);
  const blockchain = useSelector((state) => state.blockchain);
  
  // console.log(data);

  // Mint de un nuevo Token NFT
  const mintNFT = async (_account, valor, packId) => {
    setLoading(true);   
    blockchain.preventa.methods
      .createPack(packId)
      .send({
        from: _account,
        value: blockchain.web3.utils.toWei(valor, "ether"),
      })
      .once("error", (err) => {
        setLoading(false);
        console.log(err);
      })
      .then((receipt) => {
        setLoading(false);
        console.log(receipt);
        dispatch(fetchData(blockchain.account));
      });
  };
 

  // Recurso: https://es.reactjs.org/docs/hooks-effect.html
  // De forma similar a componentDidMount y componentDidUpdate
  useEffect(() => {
    if (blockchain.account != "" && blockchain.preventa != null && blockchain.account) {
      dispatch(fetchData(blockchain.account));
    }
  }, [blockchain.preventa, blockchain.account]);


  const getMinimizeAdress = (address) => {
  
    return address.substring(0, 4) + "..." + address.substring(address.length - 7, address.length);
  }

  return (
    <div  >

    <Header actualPage={"Market"} />
    <div style={{ paddingTop: 10 }} >
   
        <section className="tf-section degradadoAzul live-auctions style1 pad-b-54 mobie-style">
          <div className="themesflat-container">
            <div
              style={{ marginTop: 45, marginBottom: 45 }}
              className="page-title-heading"
            >
              <h1 className="heading text-center">Buy your PreSale Eggs</h1>
            </div>

            <div className="row">
              <div className="col-md-12">
                <div className="heading-live-auctions"></div>
              </div>
              <div className="col-md-12">
                <div
                  className="
                  swiper-container
                  show-shadow
                  carousel
                  pad-t-17
                  auctions
                "
                >
                  <div className="swiper-wrapper">
                    <Item
                      id={1}
                      data={data}
                      max={2500}
                      price={0.1}
                      title={"White egg"}
                      cantidad={1}
                      description={"+ 0% Bonus on roll"}
                      onClick={(e) => {
                        e.preventDefault();

                        if (!blockchain.loading) {
                          if (
                            blockchain.account === "" ||
                            blockchain.preventa === null
                          ) {
                            dispatch(connect());
                          } else {                           
                            mintNFT(blockchain.account, '0.1', 1);
                          }
                        }
                      }}
                    />
                    <Item
                      id={2}
                      data={data}
                      max={1300}
                      price={0.2}
                      title={"Brown egg"}
                      cantidad={2}
                      description={"+ 5% Bonus on roll"}
                      onClick={(e) => {
                        e.preventDefault();

                        if (!blockchain.loading) {
                          if (
                            blockchain.account === "" ||
                            blockchain.preventa === null
                          ) {
                            dispatch(connect());
                          } else {                           
                            mintNFT(blockchain.account, '0.2', 2);
                          }
                        }
                      }}
                    />
                    <Item
                      id={3}
                      data={data}
                      max={600}
                      price={0.35}
                      title={"Silver egg"}
                      cantidad={3}
                      description={"+ 10% Bonus on roll"}
                      onClick={(e) => {
                        e.preventDefault();

                        if (!blockchain.loading) {
                          if (
                            blockchain.account === "" ||
                            blockchain.preventa === null
                          ) {
                            dispatch(connect());
                          } else {
                            mintNFT(blockchain.account, '0.35', 3);
                          }
                        }
                      }}
                    />
                    <Item
                      id={4}
                      max={400}
                      data={data}
                      price={0.45}
                      title={"Golden egg"}
                      cantidad={3}
                      description={"+ 15% Bonus on roll"}
                      onClick={(e) => {
                        e.preventDefault();

                        if (!blockchain.loading) {
                          if (
                            blockchain.account === "" ||
                            blockchain.preventa === null
                          ) {
                            dispatch(connect());
                          } else {
                            mintNFT(blockchain.account, '0.45', 4);
                          }
                        }
                      }}
                    />
                    <Item
                      id={5}
                      data={data}
                      max={200}
                      price={0.6}
                      title={"Diamond egg"}
                      cantidad={3}
                      description={"+ 20% Bonus on roll"}
                      onClick={(e) => {
                        e.preventDefault();

                        if (!blockchain.loading) {
                          if (
                            blockchain.account === "" ||
                            blockchain.preventa === null
                          ) {
                            dispatch(connect());
                          } else {
                            mintNFT(blockchain.account, '0.6', 5);
                          }
                        }
                      }}
                    />
                  </div>
                  <div className="swiper-pagination mg-t-6"></div>
                </div>
              </div>
            </div>

            <div className="col-md-12">
              <div className="heading-live-auctions">
                <h2 className="tf-title pb-18">Yours Cool Chicks</h2>
              </div>
            </div>

            <s.Container jc={"center"} fd={"row"} style={{ flexWrap: "wrap" }}>
              {data.allOwnerLips.map((item, index) => {
                return (
                  <ItemChick
                    item={item}
                    index={index}
                    loading={loading}                    
                    blockchain={blockchain}
                  />
                );
              })}
            </s.Container>
          </div>
        </section>

        <footer style={{ marginBottom : 20, color : 'white'}} >
          <p style={{ marginTop: 5, textAlign: "center", fontSize: 16, color : 'white' }}>
            Copyright &copy; 2021 - 2022 Cool Chick. All Right Reserved
          </p>
        </footer>
      </div>
    </div>
  );
}

export default Presale;
