import React from "react";
import ChickRenderer from "../ChickRenderer";
 

function Item(props) {

  const { item, loading, levelUpLip, blockchain } = props;


  const getNameOfRarity = (rarity) => {
    if(rarity == 0) return "COMMON";
    if(rarity == 1) return "RARE";
    if(rarity == 2) return "EPIC";
    if(rarity == 3) return "LEGENDARY";
    if(rarity == 4) return "MYSTIC";
    return "";
  }

  return (
  
    <div style={{ width: 330, margin: 10 }} className="swiper-slide">

      <div className="slider-item">
        <div style={{ backgroundColor: "#F9F9F9" }} className="sc-card-product">
          <div className="card-media active">
            <a href="item-details.html">
            <ChickRenderer lip = {item} />
            </a>
          </div>
          <div className="card-title" style={{  justifyContent:'center'   }}>
            <h3>
                           
                     {getNameOfRarity(item.rarity)} 
            </h3>
          </div>
          <div>
            <div>
             
            </div>

 


           

            
          </div>
        </div>
      </div>
    </div>
 
  );
}

export default Item;
