import Web3 from "web3"
import GameToken from "../../contracts/GameToken.json";
import GameLogic from "../../contracts/GameLogic.json";
import Reserva from "../../contracts/Reserva.json";
import { fetchData } from "../data/dataActions"

const connectRequest = () => {
    return {
        type: "CONNECTION_REQUEST",
    };
};

const connectSuccess = (payload) => {
    return {
        type: "CONNECTION_SUCCESS",
        payload: payload,
    };
};

const connectFailed = (payload) => {
    return {
        type: "CONNECTION_FAILED",
        payload: payload,
    };
};

const updateAccountRequest = (payload) => {
    return {
        type: "UPDATE_ACCOUNT",
        payload: payload,
    };
};

export const connect = () => {
    return async (dispatch) => {
        dispatch(connectRequest());
        console.log("connecting...", window.ethereum);
        if(window.ethereum) {
            let web3 = new Web3(window.ethereum);
            try {
                console.log("paso 1")
                const accounts = await web3.eth.requestAccounts();
              //  const web3_2 = new Web3(window.web3.currentProvider);
              // const accounts2 = await web3_2.eth.requestAccounts();
                console.log("paso 2",accounts);
              //  console.log("paso 3",accounts2);
                const networkId = await window.ethereum.request({method: "net_version", });
                console.log(networkId);
                // ID de la Blockchain de despliegue
                // Ganache -> 5777
                // BSC Testnet -> 97
                // MATIC/Polygon -> 80001
                // Rinkeby -> 4
                if (networkId == 97 && accounts.length > 0) {
                    const networkDataGameToken = GameToken.networks[networkId];
                    const networkDataGameLogic = GameLogic.networks[networkId];
                    const networkDataReserva = Reserva.networks[networkId];
                    const abiGameToken = GameToken.abi;
                    const abiGameLogic = GameLogic.abi;
                    const abiReserva = Reserva.abi;
                    const addressGameToken = networkDataGameToken.address;
                    const addressGameLogic = networkDataGameLogic.address;
                    const addressReserva = networkDataReserva.address;

                    const gameToken = new web3.eth.Contract(abiGameToken, addressGameToken);
                    const gameLogic = new web3.eth.Contract(abiGameLogic, addressGameLogic);
                    const reserva = new web3.eth.Contract(abiReserva, addressReserva);

                    
                    
                    window.ethereum.on("accountsChanged", (accounts) => {
                        dispatch(updateAccount(accounts[0]));
                    });

                    window.ethereum.on("chainChanged", () => {
                        window.location.reload();
                    });

                    dispatch(
                        connectSuccess({
                            account: accounts[0],
                            gameToken: gameToken,
                            gameLogic: gameLogic,
                            reserva: reserva,
                            web3: web3,
                        })
                    );

                } else {
                    dispatch(connectFailed("¡El Smart Contract no se ha desplegado en la red!"));
                    } 
                } catch(err) {
                    console.log(err);
                    dispatch(connectFailed("¡Algo ha salido mal!"))
                }
            } else {
                dispatch(connectFailed("¡Instala Metamask!"))
            }
        };
    };

    export const updateAccount = (account) => {
        return async (dispatch) => {
            dispatch(updateAccountRequest({account: account}));
            dispatch(fetchData(account));
        };
    };
