import React  from "react";

import styled from "styled-components";

const Container = styled.div`
  height: 7px;
  width: 100%;
  position: relative;
`;

const BaseBox = styled.div`
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 3px;   
`;

const Background = styled(BaseBox)`
  background: grey;
  width: 100%;
`;

const Progress = styled(BaseBox)`
  background: blue;
  width: ${({ percent }) => percent}%;
`;

export default function ProgressBar(props) {
  const { porcentaje, backgroundColor } = props;

  return (
    <div>
      <Container>
        <Background />
        <Progress style={{ backgroundColor : backgroundColor }} percent={porcentaje} />
      </Container>
    </div>
  );
}
