const initialState = {
    loading: false, 
    allLips: [],
    statusPresale: [],
    allOwnerLips: [], 
    error: false, 
    errorMsg: "",
};

const dataReducer = (state = initialState, action) => {
    switch(action.type) {
        case "CHECK_DATA_REQUEST":
            return {
                ...initialState,
                loading: true,
            };

        case "CHECK_DATA_SUCCESS":
            return {
                ...initialState, 
                loading: false,
                statusPresale: action.payload.statusPresale,
                allOwnerLips: action.payload.allOwnerLips,
                chicksOnSale: action.payload.chicksOnSale,
                balanceToken: action.payload.balanceToken,
                getDayNumerOfGame: action.payload.getDayNumerOfGame,
                pozoData: action.payload.pozoData,
                actualTime: action.payload.actualTime,
                player: action.payload.player,
                startGame: action.payload.startGame,
                balanceBNBGame: action.payload.balanceBNBGame,
                balancePOZOS: action.payload.balancePOZOS,
                balanceBNBReserva: action.payload.balanceBNBReserva,
            };

        case "CHECK_DATA_FAILED": 
            return {
                ...initialState,
                loading: false,
                error: true,
                errorMsg: action.payload,
            };
        
            default: return state
    }
};

export default dataReducer;