import React from "react";
import { partsRarity0, partsRarity1, partsRarity2, partsRarity3, partsRarity4 } from "../parts/parts";

import _r1 from "../assets/images/rarity/_rarity_1.png";
import _r2 from "../assets/images/rarity/_rarity_2.png";
import _r3 from "../assets/images/rarity/_rarity_3.png";

const ChickRenderer = ({ lip = null, size = 300, style }) => {
  if (!lip) {
    return null;
  }

  let rarityBaseUI = partsRarity0;
  const  _rarity  = parseInt( lip.rarity );
  if ( _rarity === 1 ) {
    rarityBaseUI = partsRarity1;
  }
  if ( _rarity === 2 ) {
    rarityBaseUI = partsRarity2;
  }
  if ( _rarity === 3 ) {
    rarityBaseUI = partsRarity3;
  }
  if ( _rarity === 4 ) {
    rarityBaseUI = partsRarity4;
  }


  let rarity = _r1;

  if (lip.rarity >= 80) {
    rarity = _r2;
  }

  if (lip.rarity >= 95) {
    rarity = _r3;
  }

  let dnaStr = String(lip.dna);
  while (dnaStr.length < 16) dnaStr = "0" + dnaStr;

  let lipDetails = {
    bg: dnaStr.substring(0, 2) % 8,
    mask: dnaStr.substring(2, 4) % 8,
    line: dnaStr.substring(4, 6) % 8,
    addon: dnaStr.substring(6, 8) % 8,
    addonMouth1: dnaStr.substring(8, 10) % 8,
    addonMouth2: dnaStr.substring(10, 12) % 8,
    addonMouth3: dnaStr.substring(12, 14) % 8,
    name: lip.name,
  };


  const lipStyle = {
    width: "100%",
    height: "100%",
    position: "absolute",
  };

  return (
    <div
      style={{
        minWidth: size,
        minHeight: size,        
        position: "relative",
        ...style,
      }}
    >
      {/*   FONDO  */}
      <img alt={"mask"} src={rarityBaseUI.mask[lipDetails.mask]} style={lipStyle} />

      {/*   CUERPO  */}
      <img alt={"bg"} src={rarityBaseUI.bg[lipDetails.bg]} style={lipStyle} />

      {/*   BOCA  */}
      <img alt={"line"} src={rarityBaseUI.line[lipDetails.line]} style={lipStyle} />
      
      {/*   PELO  */}
      <img alt={"addon"} src={rarityBaseUI.addon[lipDetails.addon]} style={lipStyle} />

      {/*   BUFANDA  */}
      <img
        alt={"addon_mouth"}
        src={rarityBaseUI.addonMouth1[lipDetails.addonMouth1]}
        style={lipStyle}
      />

      <img
        alt={"addon_mouth"}
        src={rarityBaseUI.addonMouth3[lipDetails.addonMouth3]}
        style={lipStyle}
      />
      {/*   LENTES  */}
      <img
        alt={"addon_mouth"}
        src={rarityBaseUI.addonMouth2[lipDetails.addonMouth2]}
        style={lipStyle}
      />
    </div>
  );
};

export default ChickRenderer;
