import store from "../store";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

export const fetchData = (account) => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    console.log("VERIFICA DATA", store.getState().data);
    if (!store.getState().loading) {
      try {
        console.log("account: ", account);

        let balanceBNBGame = await store.getState()
        .blockchain.gameLogic.methods.getBalanceToken( store.getState()
        .blockchain.gameLogic._address).call({ from: account });

        let balanceBNBReserva = await store.getState()
        .blockchain.gameLogic.methods.getBalanceToken( store.getState()
        .blockchain.reserva._address).call({ from: account });
        
        let balancePOZOS = await store.getState()
        .blockchain.gameLogic.methods.getPozoBalances().call({ from: account });

        let getDappData = await store
          .getState()
          .blockchain.gameLogic.methods.getDappData()
          .call({ from: account });
         console.log("** *** getDappData: ", getDappData);
        let getDayNumerOfGame = getDappData[0]; // await store.getState().blockchain.gameToken.methods.getDayNumerOfGame().call({from: account});
        let chicksOnSale = getDappData[1]; // await store.getState().blockchain.gameToken.methods.getNTFOnSale().call({from: account});
        let allOwnerLips = getDappData[2]; // await store.getState().blockchain.gameToken.methods.getOwnerNFTs(account).call({from: account});
        let statusPresale = getDappData[3]; // await store.getState().blockchain.preventa.methods.getStatusPresale().call({from: account});
        let balanceToken = getDappData[4]; // await store.getState().blockchain.gameLogic.methods.getBalanceToken(account).call({from: account});
        let pozoData = getDappData[5]; // await store.getState().blockchain.gameToken.methods.Pozos(getDayNumerOfGame).call({from: account});
        let actualTime = getDappData[6]; // await store.getState().blockchain.gameToken.methods.Pozos(getDayNumerOfGame).call({from: account});
        let startGame = getDappData[7];
        let player = getDappData[8];
        /*  console.log("** *** balanceToken: ", balanceToken);
            console.log("** *** allOwnerLips: ", allOwnerLips);
            console.log("** *** getDayNumerOfGame: ", getDayNumerOfGame);
            console.log("** *** pozoData: ", pozoData);*/
        dispatch(
          fetchDataSuccess({
            statusPresale,
            allOwnerLips,
            chicksOnSale,
            balanceToken,
            getDayNumerOfGame,
            pozoData,
            actualTime,
            balanceBNBGame,
            balanceBNBReserva,
            balancePOZOS,
            player,
            startGame
          })
        );
      } catch (err) {
        console.log(err);
        dispatch(
          fetchDataFailed(
            "¡No se ha podido cargar los datos del Smart Contract!"
          )
        );
      }
    }else{
        console.log("En ESPERA a respuesta de blackChain");
    }
  };
};
