import React, { useState } from "react";
import ChickRender from "../ChickRenderer";
import styled from "styled-components";
import * as s from "../../styles/globalStyles";

const Input = styled.input`
  padding: 0.5em;
  margin: 0.5em;
  color: ${(props) => props.inputColor || "palevioletred"};
  background: papayawhip;
  border: none;
  border-radius: 3px;
`;

function Item(props) {
  const { item, loading, levelUpLip, blockchain, onClick } = props;

  const [onsale, setOnSale] = useState(false);

  const [price, setPrice] = useState(0);


  const onChangePrice = (e) => {
    setPrice(e.target.value);
  };

  const getNameOfRarity = (rarity) => {
    if (rarity == 0) return "COMMON";
    if (rarity == 1) return "RARE";
    if (rarity == 2) return "EPIC";
    if (rarity == 3) return "LEGENDARY";
    if (rarity == 4) return "MYSTIC";
    return "";
  };

  const getEthPrice = (tot) => {
    if(tot)
    {
    return blockchain.web3.utils.fromWei(tot, "ether");    
    }
    else
    return 0;
  };

  const getWeight = (weight) => {
    let w = parseFloat(parseInt(weight) / 100).toFixed(2);
    return w;
  };

  return (
    <div style={{ width: 330, margin: 10 }} className="swiper-slide">
      <div className="slider-item">
        <div style={{ backgroundColor: "#F9F9F9" }} className="sc-card-product">

        

          <div className="card-media active">
            <a href="item-details.html">
              <ChickRender lip={item} />
            </a>
          </div>
          <div className="card-title" style={{ justifyContent: "center" }}>
            <h3>{getNameOfRarity(item.rarity)}</h3>
          </div>
          <div>
            {item.price > 0 ? (
              <div style={{ textAlign: "center" }}>

<div className="card-title" style={{ justifyContent: "center" }}>
                <h6 style={{ marginRight: 15 }}>Level: {item.level}</h6>
                <h6 style={{ marginRight: 15 }}>Weight: {getWeight(item.weight)} Kg.</h6>
                <h6 >Exp: {item.countTotalFeed} </h6>
              </div>
              
                <h4>Price: {getEthPrice(item.price)} BNB</h4>
                {blockchain.account == item.currentOwner ?
                <button
                  className="sc-button fl-button pri-3"
                  style={{
                    padding: 10,
                    paddingLeft: 30,
                    paddingRight: 30,
                    marginTop: 20,
                    borderRadius: 10,
                    backgroundColor: "white",
                    color: "black",
                    borderColor: "#EFF834",
                    cursor : 'default'
                  }}
                 
                >
                  <span>OWN SELF</span>
                </button>
                :
                <button
                className="sc-button fl-button pri-3"
                style={{
                  padding: 10,
                  paddingLeft: 30,
                  paddingRight: 30,
                  marginTop: 20,
                  borderRadius: 10,
                  backgroundColor: "#EFF834",
                  color: "black",
                  borderColor: "#EFF834",
                }}
                onClick={(e) => {
                  onClick(e, item.id);
                }}
              >
                <span>BUY</span>
              </button>
}
              </div>
            ) : (
              <div style={{ textAlign: "center" }}>
                {onsale ? (
                  <div>
                    <div
                      style={{
                        textAlign: "left",
                        fontSize: 20,
                        marginLeft: 10,
                      }}
                    >
                      Set amount of CChick:
                    </div>
                    <div>
                      <Input
                        defaultValue="0"
                        type="number"
                        onChange={(e) => {
                          onChangePrice(e);
                        }}
                      />
                    </div>

                    <button
                      className="sc-button fl-button pri-3"
                      style={{
                        padding: 10,
                        paddingLeft: 30,
                        paddingRight: 30,
                        marginTop: 20,
                        borderRadius: 10,
                        backgroundColor: "green",
                        color: "black",
                        borderColor: "#EFF834",
                      }}
                      onClick={(e) => {
                        if (price > 0) {
                          onClick(e, item.id, price);
                        } else {
                          alert("Please set a price");
                        }
                      }}
                    >
                      <span>CONFIRM</span>
                    </button>
                    <button
                      className="sc-button fl-button pri-3"
                      style={{
                        padding: 10,
                        paddingLeft: 30,
                        paddingRight: 30,
                        marginTop: 20,
                        borderRadius: 10,
                        backgroundColor: "red",
                        color: "black",
                        borderColor: "#EFF834",
                      }}
                      onClick={(e) => {
                        setOnSale(false);
                      }}
                    >
                      <span>CANCEL</span>
                    </button>
                  </div>
                ) : (
                  <button
                    className="sc-button fl-button pri-3"
                    style={{
                      padding: 10,
                      paddingLeft: 30,
                      paddingRight: 30,
                      marginTop: 20,
                      borderRadius: 10,
                      backgroundColor: "#EFF834",
                      color: "black",
                      borderColor: "#EFF834",
                    }}
                    onClick={(e) => {
                      setOnSale(true);
                    }}
                  >
                    <span>SALE</span>
                  </button>
                )}
              </div>
            )}
          </div>
          <div>
            <div></div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Item;
