import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../..//redux/data/dataActions";
import * as s from "../../styles/globalStyles";

import OnSaleItemChick from "./OnSaleItemChick";

function OnSale(props) {
  const { setSelectedOption } = props;

  const dispatch = useDispatch();
  const data = useSelector((state) => state.data);
  const [loading, setLoading] = useState(false);
  const blockchain = useSelector((state) => state.blockchain);

  console.log("data.chicksOnSale", data.chicksOnSale);

  const buyChick = async (_account, id) => {
    setLoading(true);

    blockchain.gameLogic.methods
      .buyChic(id)
      .send({
        from: _account,
      })
      .once("error", (err) => {
        setLoading(false);
        console.log(err);
      })
      .then((receipt) => {
        setLoading(false);
        console.log(receipt);
        dispatch(fetchData(blockchain.account));
        setSelectedOption(1);
      });
  };


  const getWeight = (weight) => {
    let w = parseFloat(parseInt(weight) / 100).toFixed(2);
    return w;
  };

  
  return (
    <div className="themesflat-container">
      <div
        style={{ marginTop: 45, marginBottom: 45 }}
        className="page-title-heading"
      >
        <h1 className="heading text-center">Available Cool Chicks</h1>
      </div>

      <div>
        <s.Container jc={"center"} fd={"row"} style={{ flexWrap: "wrap" }}>
          {data && data.chicksOnSale && (
            <>
              {data.chicksOnSale.map((item, index) => {
                return (
                  <OnSaleItemChick
                    item={item}
                    index={index}
                    loading={loading}
                    blockchain={blockchain}
                    onClick={(e, id) => {
                      e.preventDefault();
                      console.log("id", id);

                      if (!blockchain.loading) {
                        if (
                          blockchain.account === "" ||
                          blockchain.gameLogic === null
                        ) {
                          dispatch(connect());
                        } else {
                          buyChick(blockchain.account, id);
                        }
                      }
                    }}
                  />
                );
              })}
            </>
          )}
        </s.Container>
      </div>
    </div>
  );
}

export default OnSale;
