import React, {   useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../..//redux/data/dataActions";
 
import Item from "./Item";
 

function BuyEgg(props) {

    const {setSelectedOption} = props;

    const dispatch = useDispatch();
    const data = useSelector((state) => state.data);
    const [loading, setLoading] = useState(false);
    const blockchain = useSelector((state) => state.blockchain);


  // Mint de un nuevo Token NFT
  const mintNFT = async (_account, valor, packId) => {
    setLoading(true);
    
    blockchain.preventa.methods
      .createPack(packId)
      .send({
        from: _account,
        value: blockchain.web3.utils.toWei(valor, "ether"),
      })
      .once("error", (err) => {
        setLoading(false);
        console.log(err);
      })
      .then((receipt) => {
        setLoading(false);
        console.log(receipt);
        dispatch(fetchData(blockchain.account));
        setSelectedOption(1);
      });
  };

 
 

  return (
    <div className="themesflat-container">
      <div
        style={{ marginTop: 45, marginBottom: 45 }}
        className="page-title-heading"
      >
        <h1 className="heading text-center">Buy your Eggs</h1>
      </div>

      <div className="row">
        <div className="col-md-12">
          <div className="heading-live-auctions"></div>
        </div>
        <div className="col-md-12">
          <div
            className="
        swiper-container
        show-shadow
        carousel
        pad-t-17
        auctions
      "
          >
            <div className="swiper-wrapper">
              <Item
                id={1}
                data={data}
                max={2500}
                price={0.1}
                title={"White egg"}
                cantidad={1}
                description={"+ 0% Bonus on roll"}
                onClick={(e) => {
                  e.preventDefault();

                  if (!blockchain.loading) {
                    if (
                      blockchain.account === "" ||
                      blockchain.preventa === null
                    ) {
                      dispatch(connect());
                    } else {
                     
                      mintNFT(blockchain.account, "0.1", 1);
                    }
                  }
                }}
              />
              <Item
                id={2}
                data={data}
                max={1300}
                price={0.2}
                title={"Brown egg"}
                cantidad={2}
                description={"+ 5% Bonus on roll"}
                onClick={(e) => {
                  e.preventDefault();

                  if (!blockchain.loading) {
                    if (
                      blockchain.account === "" ||
                      blockchain.preventa === null
                    ) {
                      dispatch(connect());
                    } else {
                    
                      mintNFT(blockchain.account, "0.2", 2);
                    }
                  }
                }}
              />
              <Item
                id={3}
                data={data}
                max={600}
                price={0.35}
                title={"Silver egg"}
                cantidad={3}
                description={"+ 10% Bonus on roll"}
                onClick={(e) => {
                  e.preventDefault();

                  if (!blockchain.loading) {
                    if (
                      blockchain.account === "" ||
                      blockchain.preventa === null
                    ) {
                      dispatch(connect());
                    } else {
                     
                      mintNFT(blockchain.account, "0.35", 3);
                    }
                  }
                }}
              />
              <Item
                id={4}
                max={400}
                data={data}
                price={0.45}
                title={"Golden egg"}
                cantidad={3}
                description={"+ 15% Bonus on roll"}
                onClick={(e) => {
                  e.preventDefault();

                  if (!blockchain.loading) {
                    if (
                      blockchain.account === "" ||
                      blockchain.preventa === null
                    ) {
                      dispatch(connect());
                    } else {
                     
                      mintNFT(blockchain.account, "0.45", 4);
                    }
                  }
                }}
              />
              <Item
                id={5}
                data={data}
                max={200}
                price={0.6}
                title={"Diamond egg"}
                cantidad={3}
                description={"+ 20% Bonus on roll"}
                onClick={(e) => {
                  e.preventDefault();

                  if (!blockchain.loading) {
                    if (
                      blockchain.account === "" ||
                      blockchain.preventa === null
                    ) {
                      dispatch(connect());
                    } else {
                     
                      mintNFT(blockchain.account, "0.6", 5);
                    }
                  }
                }}
              />
            </div>
            <div className="swiper-pagination mg-t-6"></div>
          </div>
        </div>
      </div>

     

      
    </div>
  );
}

export default BuyEgg;
