import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../..//redux/data/dataActions";
import * as s from "../..//styles/globalStyles";
 
import ItemChick from "./ItemChick";

function MyChiks(props) {
  const dispatch = useDispatch();
  const data = useSelector((state) => state.data);
  const [loading, setLoading] = useState(false);
  const blockchain = useSelector((state) => state.blockchain);

  const setForSale = async (_account, id, price) => {
    setLoading(true);
    const valorToSale = parseFloat(price);
    
    console.log("valor To SALE", valorToSale);
    
    const _forSale = valorToSale > 0 ? true : false;
    const _price = _forSale ? blockchain.web3.utils.toWei(valorToSale.toString(), "ether") : 0;
    console.log("valor To _price", _price);
    blockchain.gameLogic.methods
      .setForSale(id, _forSale, _price )
      .send({
        from: _account,
      })
      .once("error", (err) => {
        setLoading(false);
        console.log(err);
      })
      .then((receipt) => {
        setLoading(false);
        console.log(receipt);
        dispatch(fetchData(blockchain.account));
      });
  };

  return (
    <div className="themesflat-container">
      <div
        style={{ marginTop: 45, marginBottom: 45 }}
        className="page-title-heading"
      >
        <h1 className="heading text-center">Yours Cool Chicks</h1>
      </div>

      <div  >
        

        <s.Container jc={"center"} fd={"row"} style={{ flexWrap: "wrap" }}>
          {data.allOwnerLips.map((item, index) => {
            return (
              <ItemChick
                item={item}
                index={index}
                loading={loading}
                blockchain={blockchain}
                onClick={(e, id, price) => {
                  e.preventDefault();
                  console.log("id", id);
                  console.log("price", price);

                  if (!blockchain.loading) {
                    if (
                      blockchain.account === "" ||
                      blockchain.gameLogic === null
                    ) {
                      dispatch(connect());
                    } else {
                      setForSale(blockchain.account, id, price);
                    }
                  }
                }}
              />
            );
          })}
        </s.Container>
      </div>
    </div>
  );
}

export default MyChiks;
