import React, { useState, useEffect } from "react";
import Modal from "react-awesome-modal";
import styled from "styled-components";
import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "../../redux/data/dataActions";
import Spinner from "../Spinner";

const Input = styled.input`
  padding: 0.5em;
  margin: 0.5em;
  width: 30px;
  color: black;
  background: white;
  border: none;
  border-radius: 3px;
`;

function FlagsControl(props) {
  const { accionName, colorBoton, funcionBC, placeholder } = props;

  const [respuesta, setRespuesta] = useState("");
  const [inputData, setInputData] = useState("");
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const dispatch = useDispatch();
  const data = useSelector((state) => state.data);
  const blockchain = useSelector((state) => state.blockchain);

  useEffect(() => {}, []);


  const setFlag = async ( key, value) => {
    setLoading(true);

    funcionBC(inputData)
      .send({
        from: blockchain.account,
      })
      .once("error", (err) => {
        setLoading(false);
        setRespuesta("ERROR");
        console.log(err);
      })
      .then((receipt) => {
        setLoading(false);
        setRespuesta("CORRECTO");
        console.log(receipt);
        dispatch(fetchData(blockchain.account));
      });
  };

  /*
  const getData = async (key) => {
    setLoading(true);

    let ret = await funcionBC(inputData).call({ from: blockchain.account });

    console.log("**** >>>", ret);

    setLoading(false);
    if (ret === true || ret === false) {
      setRespuesta(ret ? "SI" : "NO");
    } else {
      let respuesta = JSON.stringify(ret);
      if (respuesta.length > 0) {
        if (respuesta[0] != "{" && respuesta[0] != "[") {
          respuesta = ret;
        }
      }
      setRespuesta(respuesta);
    }
    setVisible(true);
  };
*/

  return (
    <div
      style={{
        backgroundColor: "#CA6B00",
        padding: 7,
        borderRadius: 5,
        margin: 5,
      }}
    >
      {!loading ? (
        <div>
          <Input
            placeholder={placeholder}
            style={{ width: 120, height: 35, backgroundColor: "white", fontSize : 14, textAlign: 'center' }}
            type="text"
            value={inputData}
            onChange={(e) => setInputData(e.target.value)}
          />
          <button
            style={{
              padding: 7,
              borderRadius: 5,
              marginLeft: 3,
              backgroundColor: colorBoton,
              marginBottom: 5,
            }}
            onClick={() => setFlag()}
          >
            {accionName}
          </button>
        </div>
      ) : (
        <>
          <Spinner />
        </>
      )}

      <Modal
        visible={visible}
        width="990"
        height="600"
        effect="fadeInUp"
        onClickAway={() => setVisible(false)}
      >
        <div>
          <div style={{ color: "black", textAlign: "center", marginTop: 30 }}>
            <h3> {accionName}</h3>
            <div style={{ width: 980, height: 520, overflowY: "scroll" }}>
              <span> {respuesta} </span>
            </div>
          </div>
        </div>
      </Modal>
    </div>
  );
}

export default FlagsControl;
