import React, { useState, useEffect } from "react";

import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "../../redux/data/dataActions";
import Spinner from "../Spinner";
import styled from "styled-components";
 
const Input = styled.input`
  padding: 0.5em;
  margin: 0.5em;
  color: black;
  background: white;
  border: none;
  border-radius: 3px;
`;

const Select = styled.select`
  width: 300px;
  height: 35px;   
  color: black;
  padding-left: 5px;
  font-size: 14px;
  border: none;
  margin-left: 10px;

  option {
    color: black;
    background: white;
    display: flex;
    white-space: pre;
    min-height: 20px;
    padding: 0px 2px 1px;
  }
`;

function FlagsControl(props) {
  const {} = props;

  const [estado, setEstado] = useState("");
  const [actualFlag, setActualFlag] = useState("");
  const [loading, setLoading] = useState(false);
  const dispatch = useDispatch();
  const data = useSelector((state) => state.data);
  const blockchain = useSelector((state) => state.blockchain);

  const flags = [
    {
      name: "PRESALE - ESTADO",
      id: "PRESALE",
    },
    {
      name: "EGGS - VENTA DE HUEVOS PERMITIDA",
      id: "EGGS",
    },
    {
      name: "FEED - ALIMENTACIÓN PERMITIDA",
      id: "FEED",
    },
    {
      name: "MARKET - HABILITADO",
      id: "MARKET",
    },
    {
      name: "SALE - SE PERMITE VENDER POLLITOS",
      id: "SALE-PAUSE",
    },
    {
      name: "GAME - PAUSA",
      id: "GAME-PAUSE",
    },
  ];

  useEffect(() => {}, []);


  const setFlag = async ( key, value) => {
    setLoading(true);

    blockchain.gameToken.methods._changeFlagFee(key, value)
      .send({
        from: blockchain.account,
      })
      .once("error", (err) => {
        setLoading(false);
        console.log(err);
      })
      .then((receipt) => {
        setLoading(false);
        console.log(receipt);
        dispatch(fetchData(blockchain.account));
      });
  };

 
  const getFlag = async ( key) => {
    setLoading(true);    
    setActualFlag(key);

    let ret = await blockchain.gameLogic.methods._getFlagFee(key).call({ from: blockchain.account });

    console.log("**** >>>", estado);

    setLoading(false);

    setEstado( ret ? "ACTIVADO" : "DESACTIVADO" );
 
  };

  const changeFlags = async (e) => {
  console.log("e.target.value: ", e.target.value);
  await getFlag(e.target.value);

  }

  const changeFlagsStatus = async (e) => {
    let conf = window.confirm("¿Está seguro de cambiar " + actualFlag + " al estado: " + estado + "?");
    if(conf)
    {
      setFlag(actualFlag, estado == "ACTIVADO" ? true : false);
    }
  }

  return (
    <div>
      
      <Select onChange={(e) => changeFlags(e)}>
        <option value="">Select a flag</option>
        {flags.map((flag, index) => (
          <option key={index} value={flag.id}>
            {flag.name}
          </option>
        ))}
      </Select>
      {!loading ?
           <span style={{ marginLeft : 10, cursor : 'pointer' }} onClick={(e) => setEstado( estado == "ACTIVADO" ? "DESACTIVADO" : "ACTIVADO" )}> <input
            name="Estado"
            type="checkbox"
            checked={estado === "ACTIVADO"}
            
            style={{ marginRight: 10 }}
            />{estado}</span> 
            :
            <><Spinner /></>
      }
      <button style={{ padding : 7, borderRadius: 5, marginLeft : 10, backgroundColor: 'blue', marginBottom: 5 }} onClick={() => changeFlagsStatus()}>ENVIAR</button>
     
    </div>
  );
}

export default FlagsControl;
