import React from "react";
// import styled from 'styled-components';
import Spinner from "./Spinner";

const Popup = ({ isShowLogin, message, statusOK, statusError }) => (
  <div style={{ zIndex : 99, textAlign: "center" }} className={`login-form ${!isShowLogin ? "active" : ""} show`}>
    <div>
      <div style={{ textAlign: "center" }} className="form-box solid">
        <h3 style={{ color: "white" }}>{message}</h3>
        {statusOK && (
          <i
            style={{ fontSize: 40, padding: 15, color: "green" }}
            className="fa-solid fa-circle-check"
          ></i>
        )}
        {statusError && (
          <i
            style={{ fontSize: 40, padding: 15, color: "red" }}
            className="fa-solid fa-face-frown"
          ></i>
        )}
        <div style={{ textAlign: "center", marginLeft : 120 }}>
        {!statusOK && !statusError && <Spinner />}
        </div>
      </div>
    </div>
  </div>
);

export default Popup;
